* {
    box-sizing: border-box;
}

body {
    margin: 0;
    font-family: 'Inter', sans-serif;
}
.nav-bar{
    transition: all 0.3s ease;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 20px;
    height: 91px;
    -webkit-transition: all 0.3s ease;
    -moz-transition: all 0.3s ease;
    -ms-transition: all 0.3s ease;
    -o-transition: all 0.3s ease;
}
.nav-bar.dark{
    background: #21222A;
}
.nav-bar.light{
    background: #FFFFFF;
}
.nav-logo{
    display: flex;
    align-items: center;
    gap:10px;
    color: #61DAFB;
}
.nav-logo .img-logo{
    width: 28px;
}
.nav-logo .text-logo{
    font-size: 22px;
    font-weight: 700;
}
.nav-title{
    color: #DEEBF8;
    font-size: 16px;
    font-weight: 600;
}
.main-content{
    transition: all 0.3s ease;
    position: relative;
    padding: 0 20px;
    height: calc(100vh - 91px);
    z-index: 1;
}
.main-content.dark{
    background-color: #282D35;
    color: #FFFFFF;
}
.main-content.light{
    background: #FFFFFF;
    color: #2B283A;
}
.main-title{
    margin: 0;
    padding: 40px 0;
    font-size: 40px;
    font-weight: 700;
}
.facts-list{
    display: flex;
    flex-direction: column;
    gap: 15px;
    margin: 0;
    list-style: none;
}
.facts-list >*{
    font-size: 16px;
    position: relative;
}
.facts-list >*::before{
    content: "";
    position: absolute;
    left: -25px;
    top: 50%;
    transform: translateY(-50%);
    -webkit-transform: translateY(-50%);
    -moz-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    -o-transform: translateY(-50%);
    width: 10px;
    height: 10px;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
    background-color: #61DAFB;
}
.background-react-logo{
    position: absolute;
    right: 0;
    top: 45px;
    z-index: -1;
}
.switcher{
    display: flex;
    align-items: center;
    gap: 5px;
}
.switcher .switcher-btn{
    transition: all 0.3s ease;
    position: relative;
    width: 40px;
    border-radius: 20px;
    -webkit-border-radius: 20px;
    -moz-border-radius: 20px;
    -ms-border-radius: 20px;
    -o-border-radius: 20px;
    height: 20px;
    cursor: pointer;
}

.switcher.dark .switcher-btn{
    background-color: #F5F5F5;
}
.switcher.light .switcher-btn{
    background-color: #2B283A;
}
.switcher .switcher-btn .switcher-ball{
    transition: all 0.3s ease;
    position: absolute;
    width: calc(20px - 3px);
    height: calc(20px - 3px);
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
    left: 3px;
    top: 50%;
    transform: translateX(0%) translateY(-50%);
    -webkit-transform: translateX(0%) translateY(-50%);
    -moz-transform: translateX(0%) translateY(-50%);
    -ms-transform: translateX(0%) translateY(-50%);
    -o-transform: translateX(0%) translateY(-50%);
    -webkit-transition: all 0.3s ease;
    -moz-transition: all 0.3s ease;
    -ms-transition: all 0.3s ease;
    -o-transition: all 0.3s ease;
}
.switcher.dark .switcher-btn .switcher-ball{
    background-color: #2B283A;
    transform: translateX(calc(40px - 23px)) translateY(-50%);
    -webkit-transform: translateX(calc(40px - 23px)) translateY(-50%);
    -moz-transform: translateX(calc(40px - 23px)) translateY(-50%);
    -ms-transform: translateX(calc(40px - 23px)) translateY(-50%);
    -o-transform: translateX(calc(40px - 23px)) translateY(-50%);
}
.switcher.light .switcher-btn .switcher-ball{
    background-color: #F5F5F5;
}
.switcher .mode-text{
    transition: all 0.3s ease;
    color: #918E9B;
    font-weight: 600;
    cursor: pointer;
}
.switcher.dark .mode-text.active{
    color: #FFFFFF;
}
.switcher.light .mode-text.active{
    color: #2B283A;
}